import React,{useState}from 'react';
import blog1 from '../images/blog1.webp';
import blog02 from '../images/blog02.webp';
import blog03 from '../images/blog03.webp';
import gallery1 from '../images/gallery1.webp';
import gallery2 from '../images/gallery2.webp';
import gallery3 from '../images/gallery3.webp';
import gallery4 from '../images/gallery4.webp';
import gallery5 from '../images/gallery5.webp';
import gallery6 from '../images/gallery6.webp';




function Gallery() {
  const[image,setImage]= useState([
    { img_path: blog1, caption1: 'Wedding Photography' , caption2:'Duis aute irure dolor in reprehenderit in'},
    { img_path: blog02, caption1: 'Wedding Photography' , caption2:'Duis aute irure dolor in reprehenderit in'},
    { img_path: blog03, caption1: 'Wedding Photography' , caption2:'Duis aute irure dolor in reprehenderit in'}
  ]);


  const [galleryFirst, setGalleryFirst]= useState([
    {img_path:gallery1},
    {img_path:gallery2},
    {img_path:gallery3}

  ]);
  const [gallerySecond, setGallerySecond]= useState([
    {img_path:gallery4},
    {img_path:gallery5},
    {img_path:gallery6}

  ]);

    return (
    <>
      <div id="page3" >

       {image.map(img =>{
         return(
          <div id="photo1" style={{overflow: 'hidden'}}>
          <div className="zoom" ><img src={img.img_path} alt="logo"/> </div>
          <div id="first_caption" style={{paddingTop: '8px'}}>
           <div style={{padding: '80px 20px 20px 0px',color:' white', fontSize: '20px', fontWeight: 'bolder'}}>Wedding Photography
           </div>
            <div style={{padding: '10px 20px 20px 0px',color:'white'}}>Duis aute irure dolor in reprehenderit in <br/> <br/>voluptate velit.
           </div>
         </div>
        </div>

         )
       })}
      </div>


    <div id="page4">
      <div id="part1">  
      {galleryFirst.map(img =>{
        return(
          <div><img src={img.img_path} alt="logo" width="100%" height="500px" /></div>

        )
      })}

      </div>
      <div id="part2">
      {gallerySecond.map(img =>{
        return(
          <div><img src={img.img_path} alt="logo" width="100%" height="500px" /></div>

        )
      })}
      </div>
    </div>
  </>
  

    )
}

export default Gallery;
