import React from "react";
import logo from '../images/logo.webp';


function Banner() {
  return (
    <div id="banner">
      <div id="top" class="top">
        <div id="logo">
          <img src={logo} alt="logo" />
        </div>
        <div id="navigator">
          <ul id="nav">
            <li>Home</li>
            <li>About</li>
            <li>Gallery</li>
            <li>Services</li>
            <li>Blog</li>
            <li>Contact</li>
          </ul>
          <div id="contact" class="btn">
            CONTACT NOW
          </div>
        </div>
      </div>
      <div id="content">
        <div id="first">-CREATIVE PHOTOGRAPHEY</div>
        <div id="second">
          PHOTOGRAPHY <br /> MAKES US HAPPY <br/> TAKE A SHOT.
        </div>
        <div id="portfolio" class="btn">
          WATCH PORTFOLIO
        </div>
      </div>
    </div>
  );
}

export default Banner;
