import React,{useState} from 'react';
import blog1 from '../images/blog1.webp';
import blog2 from '../images/blog2.webp';
import blog3 from '../images/blog3.webp';





function Blog() {


  const [loop, setLoop] = useState([
  {  img_path: blog1, date: '23 DEC,2020' , caption:'Addiction When Gambling Becomes'},
  { img_path: blog2,date: '13 FEB,2020' , caption:'Addiction When Drinking Becomes'},
  { img_path: blog3,date: '03 MAR,2020' , caption:'Addiction When Smoking Becomes'}]);

    return (
        <>
        <div id="page8">
      <div id="page8_caption">
        <div style={{color: 'white', fontSize: '50px',  textAlign: 'center', fontWeight: 'bold'}}>TOURIST BLOG</div>
        <div style={{color: 'white', fontSize: '16px',  textAlign: 'center', padding: '20px'}}>Our Recent Photos</div>
      </div>
      
      <div id="page8_images"> 
            {loop.map(obj =>{
              return(
            
              <div id="image">
                 <div><img src={obj.img_path} alt="" /></div>
                 <div id="des">
                  <div style={{color:'white'}}> {obj.date}</div>
                  <div style={{color:'white'}}>{obj.caption}</div>
                </div>
                </div>  
            
          
          );
          
            })}
        </div>
    </div>
        </>
    )
}

export default Blog;
