import React from 'react';
import testimonial from '../images/testimonial.webp';
import Homepage_testi from '../images/Homepage_testi.webp'

function Testimony() {
    return (
        
    <div id="page7" >
    <div id="main_testimony">
      <div id="testimonial_content1">
        <div style={{textAlign: 'center'}}> <img src={testimonial.webp} alt=""/></div>
        <div style={{color: 'white', textAlign: 'center',   paddingTop: '20px'}}> Remaining essentially unchanged It
          was popularised in the1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop
          publishing software like Aldus PageMaker including versions of Lorem Ipsum.

        </div>
      </div>
      <div id="testimonial_content2" style={{display: 'flex', marginLeft: '413px',
      padding: '30px'}}>
        <div style={{padding: '20px'}}><img src={Homepage_testi} alt=""/></div>
        <div id="name_post" style={{padding: '20px'}}>
          <div style={{color: '#F04E3C', fontWeight: 'bolder', fontSize: '32px'}}>Jessya Inn</div>
          <div style={{color: '#3F4959', fontSize: '16px', fontWeight: 'bolder'}}>Chif Photographer</div>
        </div>
      </div>
    </div>
  </div>
    )
}

export default Testimony;
