import React from 'react';
import logo from '../images/logo.webp';
import instagram1 from '../images/instagram1.webp';
import instagram2 from '../images/instagram1.webp';
import instagram3 from '../images/instagram1.webp';
import instagram4 from '../images/instagram1.webp';
import instagram5 from '../images/instagram1.webp';
import instagram6 from '../images/instagram1.webp';



function Contact() {
    return (
        <>
        
    <div id="page9" >
        <div id="page9_first">
        <div> <img src={logo} alt=""/></div>
        <div style={{color: 'white', fontSize: '20px', paddingTop: '20px', lineHeight: '1.5'}}>Grabbing the concsumer’s
          attention <br/>isn’t enough you have to keep that<br/> attention for at least a.</div>
      </div>
      <div id="page9_second" style={{lineHeight: '1.9'}}>
        <ul id="navigation" style={{color:'#648298'}}>
          <li style={{paddingBottom: '30px', color: 'white'}}>NAVIGATION</li>
          <li>Home</li>
          <li>Events</li>
          <li>Testimonial</li>
          <li>Categories</li>
          <li>Contacts</li>
        </ul>
      </div>

         <div id="page9_third" style={{lineHeight: '1.9'}}>
        <ul id="useful_links" style={{color: '#648298'}}>
          <li style={{paddingBottom: '30px', color: 'white'}}>USEFUK LINKS</li>
          <li>Registration</li>
          <li>Login</li>
          <li>Policy</li>
          <li>Terms and Conditions</li>
        </ul>
      </div>
        <div id="page9_fourth" style={{lineHeight: '1.9'}}>
        <div style={{color: 'white', paddingBottom: '30px'}}> INSTAGRAM FEED</div>
        <div id="page9_images">
          <div id="img_part1" style={{display: 'grid',
          gridTemplateColumns: 'auto auto auto'}}>
            
            <div><img src={instagram1} alt="" /></div>
            <div><img src={instagram2} alt="" /></div>
            <div><img src={instagram3} alt="" /></div>
          </div>

          <div id="img_part2" style={{display: 'grid',
          gridTemplateColumns: 'auto auto auto'}}>
            <div><img src={instagram4} alt="" /></div>
            <div><img src={instagram5} alt="" /></div>
            <div><img src={instagram6} alt="" /></div>
          </div>
        </div>
      </div>
      </div>

        </>
    )
}

export default Contact
