import React from 'react';
import { AiOutlineFacebook } from 'react-icons/ai';
import { FiTwitter } from 'react-icons/fi';
import {HiOutlineGlobeAlt} from 'react-icons/hi';
import {AiOutlineInstagram} from 'react-icons/ai';



function Footer() {
    return (
        <>
        
      <footer style={{display:'flex', backgroundColor: '#121212', padding: '100px'}} >
      <div style={{display: 'flex',  color:'#648298'}}>Copyright ©2021 All rights reserved | This template is made with &nbsp;
        <div><i class="fa fa-heart" aria-hidden="true" style={{color: '#F04E3C'}}></i></div> &nbsp; by <div
          style={{color: '#F04E3C'}}> &nbsp;Colorlib </div>
      </div>
      <div style={{ color: 'white', fontWeight: 'bold', paddingLeft: '255px'}}>Follow Us </div>
      <div>
        <hr id="footer_line"/>
      </div>
      <div class="padding"><AiOutlineFacebook color="white"/>
      </div>
      <div class="padding"><FiTwitter color="white"/>
      </div>
      <div class="padding"><HiOutlineGlobeAlt color="white"/>
      </div>
      <div class="padding"><AiOutlineInstagram color="white"/>
      </div>
    </footer>
        </>
    )
}

export default Footer;
